/* base styles */
* {
  margin: 0;
  font-family: "Quicksand";
  color: #333;
}
.navbar {
    background-color: #333;
    color: white;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: flex-end;
  
}

nav a {
    color: white;
    text-decoration: none;
    padding: 0 20px;
    height: 100%;
    display: flex;
    align-items: center;
  }
  
  /* Style the active link */
  nav a.active {
    background-color: #4CAF50;
  }
  

  .dropdown {
  position: relative;
  display: inline-block;
}

.dropdown-content {
  display: none;
  position: absolute;
  z-index: 1;
}

.dropdown:hover .dropdown-content {
  display: block;
}

.dropdown-content a {
  display: block;
  padding: 10px;
  background-color: #f9f9f9;
  color: black;
}

.dropdown-content a:hover {
  background-color: #ddd;
}
  
  
  